import React from "react";
import { createRoot } from "react-dom/client";
import App from "./App";

export default ({ el, basename }) => {
  const root = createRoot(el);

  const app = root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );

  root.$destroy = () => root.unmount();

  console.log('react', { app, root })

  return root;
}